import uniIcons from '../../components/uni-icons/uni-icons.vue';
import uniSection from '../../components/uni-section/uni-section.vue';
export default {
	components: { uniIcons, uniSection },
	data() {
		return {
			// 图片显示隐藏
			imageVis: false,
			// 邀请二维码的地址
			imgUrl: '',
			providerList: [],
			show1: false,
			isDom: true,
			canvasUrl: '',
			// 海报背景图片地址
			cover: 'https://www.vzoyo.com/uploads/share.png',
			hascanvas: false
		};
	},
	// onShareAppMessage(res) {
	// 	let pid = JSON.parse(uni.getStorageSync('login')).user_id;
	// 	if (res.from === 'button') {
	// 		// 来自页面内分享按钮
	// 		console.log(res.target);
	// 		title: '弹出分享时显示的分享标题';
	// 		desc: '分享页面的内容';
	// 		path: `/pages/anchor/anchor?pid=${pid}`;
	// 		imgUrl: this.imgUrl
	// 	}
	// 	return {
	// 		title: '分享标题',
	// 		path: `/pages/anchor/anchor?pid=${pid}`,
	// 		imgUrl: "https://ww1.yunjiexi.club/2020/03/18/GwFBk.png"
	// 	};
	// },
	onShareAppMessage(res) {
		let pid = JSON.parse(uni.getStorageSync('login')).user_id;
		if (res.from === 'button') {
			// 来自页面内分享按钮
			console.log(res.target);
		}
		return {
			title: '天天来提现',
			path: `/pages/anchor/anchor?pid=${pid}`,
			imgUrl: "https://ww1.yunjiexi.club/2020/03/18/GwFBk.png"
		};
	},
	methods: {
		open(e) {
			console.log(e);
			if (e.detail.authSetting['scope.writePhotosAlbum']) {
				this.show1 = false;
			}
		},
		preview() {
			wx.previewImage({
				current: this.canvasUrl, // 当前显示图片的http链接
				urls: [this.canvasUrl] // 需要预览的图片http链接列表
			});
		},
		closeimg() {
			this.imageVis = false;
		},
		getImg() {
			const that = this;
			this.imgUrl = uni.getStorageSync('login').spread_code;
			this.imageVis = true;
			if (!that.hascanvas) {
				uni.showToast({
					icon: 'loading',
					title: '正在生成海报中',
					duration: 1000
				});
			}
			// 绘制canvas
			let ctx = uni.createCanvasContext('firstCanvas');
			uni.getImageInfo({
				src: that.cover,
				success(res) {
					console.log(res);
					const path = res.path;
					ctx.drawImage(path, 0, 0, uni.upx2px(500), uni.upx2px(878));
					// ctx.fillRect(uni.upx2px(165), uni.upx2px(650), uni.upx2px(200), uni.upx2px(200));
					uni.getImageInfo({
						src: that.imgUrl,
						success(res) {
							const path1 = res.path;
							ctx.drawImage(path1, uni.upx2px(165), uni.upx2px(650), uni.upx2px(200), uni.upx2px(200));
							ctx.draw(false, () => {
								uni.canvasToTempFilePath({
									x: 0,
									y: 0,
									canvasId: 'firstCanvas',
									success: function(res) {
										that.canvasUrl = res.tempFilePath;
										that.hascanvas = true;
									},
									fail(e) {
										uni.showToast({
											title: '生成海报失败',
											icon: 'none'
										});
									}
								});
							});
						}
					});
				},
				fail(error) {
					console.log(error);
				}
			});
		},
		//点击保存图片
		save() {
			let that = this;
			//若二维码未加载完毕，加个动画提高用户体验
			uni.showToast({
				icon: 'loading',
				title: '正在保存图片',
				duration: 1000
			});
			//判断用户是否授权"保存到相册"
			wx.getSetting({
				success(res) {
					//没有权限，发起授权
					if (!res.authSetting['scope.writePhotosAlbum']) {
						wx.authorize({
							scope: 'scope.writePhotosAlbum',
							success() {
								//用户允许授权，保存图片到相册
								// 生成动态图片保存到相册

								that.savePhoto();
							},
							fail() {
								console.log('用户拒绝授权，跳转到设置页面');
								//用户点击拒绝授权，跳转到设置页，引导用户授权
								that.show1 = true;
							}
						});
					} else {
						//用户已授权，保存到相册
						that.savePhoto();
					}
				}
			});
		},
		//保存图片到相册，提示保存成功
		savePhoto() {
			wx.saveImageToPhotosAlbum({
				filePath: this.canvasUrl,
				success(res) {
					console.log(res);
					uni.showToast({
						title: '保存成功',
						icon: 'success',
						duration: 1000
					});
					that.imageVis = false;
				}
			});
		}
	}
};
